/* skill animated progress bar */
.progress-bar-container:not(:last-child) {
    margin-bottom: 50px;

}

.progress-bar-container h2 {
    font-family: Arial, Helvetica, sans-serif;
    color: #eee;
    letter-spacing: 1px;
    font-size: 1rem;
}

.persentFont {
    font-size: 9px;
    ;
}

.progress-bar {
    width: 800px;
    height: 12px;
    margin-top: 10px;
    border: 1px solid #565656;
    border-radius: 5px;
    box-shadow: 0 0 2px rgb(245, 159, 0);
}

.percentage {
    display: block;
    height: 100%;
    background-color: orange;
    border-radius: 5px;
    animation: progress 1500ms ease-in 1;
}

@keyframes progress {
    from {
        width: 0;
    }
}

