/* media query responsive */
@media only screen and (max-width: 600px) {
    .firstrow {
        font-size: 12px;
        display: flex;
        flex-direction: column;
    }

    .innerPhotoImage {
        margin-left: 0px !important;
    }

    .image img {
        height: 200px;
        width: 200px;
        object-fit: cover;
        margin-bottom: 11px;
    }

    .photocol {
        /* background-color: blue; */
        width: 102% !important;
        margin-top: 0 !important;
        display: flex;
        justify-content: center;
        padding-right: 65px;
        align-items: center;
    }

    .intro {
        margin-right: 0px !important;
        text-align: justify !important;
        width: 100% !important;
        display: flex;
        justify-content: flex-start;
        margin-top: -24px !important;
    }

    .selfnameH {
        padding-top: 0px !important;
        padding-bottom: 29px;
        margin-left: 67px;
    }

    .contentcol {
        /* background-color: green; */
        width: 100% !important;
        margin-top: 0 !important;
        margin: auto;
        width: 60%;
        align-items: center;
        padding: 10px;
    }

    .secondSession {
        /* background-color: orange; */
        display: flex;
        flex-direction: column;
        ;
    }

    .amazon {
        margin-top: 22px;
        margin-bottom: 22px;
    }

    .rowdiv {
        background-color: transparent !important;
    }

    .contentAbout {
        width: max-content;
        margin-left: 0px !important;
    }

    .aboutHead {
